<template>
  <div>
    <div v-for="comment in comments" :key="comment.id" class="jumbotron">
      <h1 class="display-9">{{comment.name}}</h1>
      <p class="'lead">{{comment.body}}</p>
      <hr class="my-4">
      <a class="btn btn-link btn-lg" :href="comment.email" role="button">{{ comment.email }}</a>
    </div>
  </div>

</template>

<script>
import axios from  'axios'

export default {
name: "Comments",
  data() {
     return {comments: []}
  },
  components: {

  },
  created() {
    axios.get('https://jsonplaceholder.typicode.com/comments').then(response => { console.log(response); this.comments = response.data})
  }

}
</script>

