<template>
<div>
<div class="title">CamControl</div>
  <div class="booth">
  <video id="video" width="400" height="300" autoplay controls="true" crossorigin="anonymous"> </video>
  <canvas id="canvas" width="400" height="300"></canvas>
  <button class="btn btn-primary mr-2 w-25" :disabled="stopped" @click="onStopped">Stop</button>
  <button class="btn btn-primary w-25" :disabled="!stopped" @click="onStarted">Start</button>

  <Comments></Comments>
  </div>
</div>
</template>

<script>
import Comments from "@/components/Comments";
export default {
  name: "CamControl",
  data() {
    return {stopped: false}
  },
  components: {Comments},
  methods: {
    onStopped() {
      this.stopped = true;
      document.getElementById('video').pause()
    },
    onStarted() {
      this.stopped = false;
      document.getElementById('video').play()
    }
  },
  mounted() {
    const video = document.getElementById('video');
    const canvas = document.getElementById('canvas');
    const context = canvas.getContext('2d');

    video.addEventListener('play', function() {
      draw(video, context, 400, 300);
      console.log('start')
    }, false)

    function draw (video, context, width,height) {
      context.drawImage(video,0 ,0, width,height);
      setTimeout(draw, 10,  video, context, width, height)
    }

// Get access to the camera!
    if(navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
        navigator.mediaDevices.getUserMedia({ video: true }).then(function(stream) {
        video.srcObject = stream;
        video.play();
      });
    }
  }
}


</script>

<style>
.booth{ width: 420px;
border: 10px solid #ddd}
video {
  visibility: hidden;
  position: absolute;
  z-index: -1;
}


</style>